* {
  box-sizing: border-box;
}

html {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

body {
  margin: 0;
}

.container {
  position: relative;

  width: 100%;
}

.slides {
  position: relative;

  height: 100vh;
  margin: 0;
  padding: 0;

  list-style-type: none;

  &__item {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    padding: 40px;

    transition: opacity 1s;

    opacity: 0;
    color: #ffffff;
    background-color: #333333;
    background-position: 50% 50%;
    background-size: cover;

    font-size: 40px;

    &.active {
      z-index: 2;

      opacity: 1;
    }

    &:nth-of-type(1) {
      background-color: red;
      background-image: url("../img/slide1.jpg");
    }

    &:nth-of-type(2) {
      background-color: orange;
      background-image: url("../img/slide2.jpg");
    }

    &:nth-of-type(3) {
      background-color: green;
      background-image: url("../img/slide3.jpg");
    }

    &:nth-of-type(4) {
      background-color: blue;
      background-image: url("../img/slide4.jpg");
    }

    &:nth-of-type(5) {
      background-color: magenta;
      background-image: url("../img/slide5.jpg");
    }
  }
}

.controls {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;

  display: none;

  width: 100%;
  height: 100%;

  &__item {
    position: absolute;

    display: flex;

    width: 10%;
    min-width: 50px;
    height: 100%;
    margin: 0;

    cursor: pointer;

    color: #ffffff;

    font-size: 40px;

    align-items: center;
    justify-content: center;
  }

  &__next {
    top: 0;
    right: 0;

    background: linear-gradient(to left, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);

    &:hover,
    &:focus {
      background: linear-gradient(to left, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0) 100%);
    }
  }

  &__prev {
    top: 0;
    left: 0;

    background: linear-gradient(to right, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);

    &:hover,
    &:focus {
      background: linear-gradient(to right, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0) 100%);
    }
  }
}

.indicators {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: calc(50% - 150px);

  display: none;

  width: 300px;
  margin-bottom: 10px;

  font-size: 12px;

  align-items: center;
  justify-content: center;

  span {
    display: flex;

    width: 24px;
    height: 24px;
    margin: 5px;

    cursor: pointer;
    text-align: center;

    color: #222222;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0 0 15px #000000;

    align-items: center;
    justify-content: center;
  }

  &__item {
    &.active {
      color: #ffffff;
      background-color: #222222;
      box-shadow: 0 0 15px #ffffff;
    }
  }
}
